import AbstractView from "./abstract-view";
import constants from "../constants";
import Application from "../application";
import FooterView from "./footer-view";

class RulesView extends AbstractView {
  constructor() {
    super();
  }

  get template() {
    return `
    <header class="header">
      <div class="header__back">
        <button class="back">
          <img src="img/arrow_left.svg" width="45" height="45" alt="Back">
          <img src="img/logo_small.svg" width="101" height="44">
        </button>
      </div>
    </header>
    <div class="rules">
      <h1 class="rules__title">–ü—Ä–∞–≤–∏–ª–∞</h1>
      <p class="rules__description">–£–≥–∞–¥–∞–π 10 —Ä–∞–∑ –¥–ª—è –∫–∞–∂–¥–æ–≥–æ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è —Ñ–æ—Ç–æ <img
        src="img/photo_icon.png" width="16" height="16"> –∏–ª–∏ —Ä–∏—Å—É–Ω–æ–∫ <img
        src="img/paint_icon.png" width="16" height="16" alt="">.<br>
        –§–æ—Ç–æ–≥—Ä–∞—Ñ–∏—è–º–∏ –∏–ª–∏ —Ä–∏—Å—É–Ω–∫–∞–º–∏ –º–æ–≥—É—Ç –±—ã—Ç—å –æ–±–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è.<br>
        –ù–∞ –∫–∞–∂–¥—É—é –ø–æ–ø—ã—Ç–∫—É –æ—Ç–≤–æ–¥–∏—Ç—Å—è 30 —Å–µ–∫—É–Ω–¥.<br>
        –û—à–∏–±–∏—Ç—å—Å—è –º–æ–∂–Ω–æ –Ω–µ –±–æ–ª–µ–µ 3 —Ä–∞–∑.<br>
        <br>
        –ì–æ—Ç–æ–≤—ã?
      </p>
      <form class="rules__form">
        <input class="rules__input" type="text" placeholder="–í–∞—à–µ –ò–º—è">
        <button class="rules__button  continue" type="submit" disabled>Go!</button>
      </form>
    </div>
    ${new FooterView().template}`;
  }

  bind() {
    const input = this.element.querySelector(`.rules__input`);
    const button = this.element.querySelector(`.rules__button`);
    const backButton = this.element.querySelector(`.back`);
    const form = this.element.querySelector(`.rules__form`);
    // –∏–º—è –Ω–µ –º–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å < —á–µ–º –∏–∑ –¥–≤—É—Ö —Å–∏–º–≤–æ–ª–æ–≤
    input.oninput = () => {
      button.disabled = input.value.length < constants.MIN_NAME_LENGTH;
    };

    button.addEventListener(`click`, (evt) => {
      evt.preventDefault();
      Application.showGame(input.value);
      form.reset();
    });

    backButton.addEventListener(`click`, () => {
      Application.showGreeting();
    });
  }
}

export default RulesView;
