import AbstractView from "./abstract-view";
import Application from "../application";
import FooterView from "./footer-view";

class GreetingView extends AbstractView {
  constructor() {
    super();
  }

  get template() {
    return `
    <div class="greeting central--blur">
      <div class="greeting__logo"><img src="img/logo_big.png" width="201" height="89" alt="Pixel Hunter"></div>
      <h1 class="greeting__asterisk">*</h1>
      <div class="greeting__challenge">
        <h3>–õ—É—á—à–∏–µ —Ö—É–¥–æ–∂–Ω–∏–∫–∏-—Ñ–æ—Ç–æ—Ä–µ–∞–ª–∏—Å—Ç—ã –±—Ä–æ—Å–∞—é—Ç&nbsp;—Ç–µ–±–µ&nbsp;–≤—ã–∑–æ–≤!</h3>
        <p>–ü—Ä–∞–≤–∏–ª–∞ –∏–≥—Ä—ã –ø—Ä–æ—Å—Ç—ã.<br>
          –ù—É–∂–Ω–æ –æ—Ç–ª–∏—á–∏—Ç—å —Ä–∏—Å—É–Ω–æ–∫&nbsp;–æ—Ç —Ñ–æ—Ç–æ–≥—Ä–∞—Ñ–∏–∏ –∏ —Å–¥–µ–ª–∞—Ç—å –≤—ã–±–æ—Ä.<br>
          –ó–∞–¥–∞—á–∞ –∫–∞–∂–µ—Ç—Å—è —Ç—Ä–∏–≤–∏–∞–ª—å–Ω–æ–π, –Ω–æ –Ω–µ –¥—É–º–∞–π, —á—Ç–æ –≤—Å–µ —Ç–∞–∫ –ø—Ä–æ—Å—Ç–æ.<br>
          –§–æ—Ç–æ—Ä–µ–∞–ª–∏–∑–º –æ–±–º–∞–Ω—á–∏–≤ –∏ –∫–æ–≤–∞—Ä–µ–Ω.<br>
          –ü–æ–º–Ω–∏, –≥–ª–∞–≤–Ω–æ–µ ‚Äî —Å–º–æ—Ç—Ä–µ—Ç—å –æ—á–µ–Ω—å –≤–Ω–∏–º–∞—Ç–µ–ª—å–Ω–æ.</p>
      </div>
      <div class="greeting__continue"><span><img src="img/arrow_right.svg" width="64" height="64" alt="Next"></span></div>
    </div>
    ${new FooterView().template}`;
  }

  bind() {
    const continueButton = this.element.querySelector(`.greeting__continue`);
    continueButton.onclick = () => {
      Application.showRules();
    };
  }

  hide() {
    this.element.children[0].style.opacity = `0`;
  }

  fadeIn() {
    this.element.children[1].style.opacity = `1`;
    this.element.children[1].classList.add(`fade--in`);
  }

  addElement(element) {
    this.element.insertAdjacentElement(`afterbegin`, element);
    this.element.children[0].style = `position: absolute; top: 0;`;
  }

  removeElement(element) {
    element.remove();
  }
}

export default GreetingView;
